import { FirebaseProvider } from './../../../providers/firebase/firebase';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { FirebaseObjectObservable, FirebaseListObservable } from 'angularfire2/database';

/**
 * Generated class for the ShopListPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-shop-list-description',
  templateUrl: 'shop-list-description.html',
})
export class ShopListDescriptionPage {
    title: FirebaseObjectObservable<any[]>;
    items: FirebaseListObservable<any[]>;
    commentary: FirebaseListObservable<any[]>;
    
    constructor(public navCtrl: NavController,
		public navParams: NavParams,
		public firebaseProvider: FirebaseProvider) {
	var rootPath = "shop-list/" + this.navParams.data + "/";
	this.title = this.firebaseProvider.getObject(rootPath);
	this.items = this.firebaseProvider.getItems(rootPath + "items");
	this.commentary = this.firebaseProvider.getItems(rootPath + "commentary");
	console.log(this.title);
    }
    
    ionViewDidLoad() {
	console.log('ionViewDidLoad CreateShopListPage');
    }

    createNewShopList() {
	
    }
}



